@use "../../global" as g;

.l-intro {
  margin-top: 50px;
  padding-bottom: 8px;

  @include g.mq(tablet) {
    margin-top: 72px;
    padding-bottom: 30px;
  }
}

.l-intro__ttl {
  font-size: 5.6rem;
  font-weight: bold;
  font-style: italic;
  color: g.$color-primary;
  line-height: 1.05;
  text-align: center;

  @include g.mq(tablet) {
    font-size: 10.8rem;
  }
}

.l-intro__subTtl {
  font-size: 1.6rem;
  font-weight: bold;
  text-align: center;

  @include g.mq(tablet) {
    font-size: 3.2rem;
  }
}
.l-intro__subTtl--emphasize {
  color: g.$color-primary;
}

.l-intro__txt {
  font-size: 1.5rem;
  line-height: 1.8;
  margin-top: 22px;

  @include g.mq(tablet) {
    font-size: 1.6rem;
    line-height: 2;
    text-align: center;
    margin-top: 21px;
  }
}