$breakpoints: (
  "sp": "screen and (max-width: 599px)",
  "tablet": "screen and (min-width: 600px)",
  "pc": "screen and (min-width: 1025px)",
) !default;

@mixin mq($breakpoint: sp) {
  @media #{map-get($breakpoints, $breakpoint)} {
    @content;
  }
}
