@use "../../global" as g;

.p-trainer__body {
  padding-top: 30px;
  padding-bottom: 40px;
}

.p-trainer__tabArea {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  cursor: pointer;
  margin-bottom: 20px;
}

.p-trainer__tab {
  font-size: 1.6rem;
  text-align: center;
  padding-bottom: 12px;

  @include g.mq(pc) {
    font-size: 1.8rem;
  }
}
.p-trainer__tab.active {
  font-weight: bold;
  position: relative;
}
.p-trainer__tab.active::after {
  content: "";
width: 100%;
height: 2px;
background-color: g.$color-white;
position: absolute;
bottom: 0;
left: 0;
}

.p-trainer__tabContent {
  display: none;
}
.p-trainer__tabContent.show {
  display: block;
}

