@use "../../global" as g;

.c-person__list {
  display: grid;
  gap: 15px;

  @include g.mq(tablet) {
    grid-template-columns:repeat(3, 1fr);
  }
}

.c-person__item {
  background-color: g.$color-white;
  border-radius: 10px;  
}

.c-person__img {
  border-radius: 10px 10px 0 0;
}

.c-person__txt {
  height: auto;
  font-size: 1.4rem;
  color: g.$color-text;
  line-height: 1.5;
  padding: 20px 20px 36px;

  @include g.mq(tablet) {
    font-size: 1.5rem;
  }
}