@use "../../global" as g;

.l-mv {
  position: relative;
  width: 100%;
  padding-top: 163px;
  padding-left: 22px;

  @include g.mq(pc) {
    padding-top: 220px;
    padding-left: 163px;
  }
}

.l-mv__ttlItem {
  position: relative;
  font-size: min(11vw, 9.0rem);
  font-style: italic;
  background-color: #fff;
  letter-spacing: 0.01em;
  line-height: 1;
}
.l-mv__ttlItem-second {
  display: inline-block;
  line-height: 1;
  padding-left: 5px;
  margin-top: 2px;
  margin-bottom: 9px;

  @include g.mq(pc) {
    margin-top: -7px;
    margin-bottom: 20px;
  }
}
.l-mv__ttlItem--ampersand {
  font-size: min(9vw, 8.5rem);
  color: g.$color-primary;
  letter-spacing: 0.03em;
}

.l-mv__subTtl {
  display: inline-block;
  font-size: min(3.5vw, 2.8rem);
  background-color: #fff;
  line-height: 1.538;
  padding-top: 2px;
}

.l-mv__btn {
  margin-top: 15px;

  @include g.mq(pc) {
    padding-top: 31px;
  }
}

.l-mv__img {
  position: absolute;
  top: 0;
  right: 0;
  width: 71%;
  height: 410px;
  background-image: url(../img/bg_mv.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  border-radius: 0 0 0 50px;
  z-index: -1;
  opacity: 0;
  animation: 3s fadeInMv 0.5s forwards;

  @include g.mq(tablet) {
    height: 680px;
  }
}