@use "../../global" as g;

.p-price__body {
  padding-top: 28px;
  padding-bottom: 40px;
  margin-right: -30px;

  @include g.mq(pc) {
    padding-top: 40px;
  }
}

.p-price__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 20px;
  position: relative;
  overflow: auto;
  padding-top: 25px;
  margin-top: -25px;

  @include g.mq(pc) {
    column-gap: 30px;
  }
}

.p-price__scroll {
  position: absolute;
  top: 50%;
  right: 5%;
  width: 80px;
  height: 70px;
  font-size: 1.2rem;
  background-color: rgb(0, 0, 0, 0.6);
  border-radius: 4px;
  text-align: center;
  transform: translateY(-50%);

  @include g.mq(pc) {
    display: none;
  }
}

.p-price__scrollIcon {
  display: block;
  font-size: 4.5rem;
  margin-top: 6px;

  @include g.mq(pc) {
    display: none;
  }
}