@use "../global" as g;

.l-header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 60px;
  transition: all 0.5s;
  z-index: 100;
}

.l-header__inner {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.l-header__logoLink {
  padding:20px;

  @include g.mq(tablet) {
    margin-left: 15px;
  }
}

.l-header__logoImg {

  @include g.mq(tablet) {
    width: 180px;
  }
}

.l-header__btn {
  display: none;
  margin-right: 20px;
  transition: all 0.5s;

  @include g.mq(tablet) {
    margin-right: 35px;
  }
}

//scroll event
.l-header.is-active {
  background-color: #fff;
  box-shadow: 0 3px 6px rgb(0, 0, 0, 5%);

  .l-header__btn {
    display: block;
  }
}