@use "../../global" as g;

.l-about {
  padding-bottom: 40px;

  @include g.mq(tablet) {
    padding-bottom: 80px;
  }
}

.l-about__box {
  margin-bottom: 40px;

  @include g.mq(tablet) {
    position: relative;
    margin-bottom: 100px;
  }
}
.l-about__box:nth-child(2n) {
  @include g.mq(pc) {
    display: flex;
    justify-content: right;
  }
}

.l-about__imgWrapper {
  width: 100%;

  @include g.mq(pc) {
    width: 70%;
  }
}

.l-about__img {
  width: 100%;
  height: auto;
  vertical-align: bottom;
}

.l-about__txtArea {
  position: relative; //手前に表示
  background: #fff;
  box-shadow: 0 0 20px rgb(0, 0, 0, 10%);
  padding: 40px 30px;
  border-radius: 8px;
  margin: -10px 20px;

  @include g.mq(pc) {
    position: absolute;
    right: 20px;
    bottom: -20px;
    width: 555px;
    padding: 60px;
  }
}
.l-about__txtArea--reverse {
  
  @include g.mq(pc) {
    left: 20px;
  }
}

.l-about__subTtl {
  font-size: 2.4rem;
  color: g.$color-primary;
  margin-bottom: 15px;
}
.l-about__subTtl--secondary {
  color: g.$color-secondary;
}

.l-about__ttl {
  font-size: 2.2rem;
  font-weight: bold;
  line-height: 1.5;
  margin-bottom: 20px;

  @include g.mq(tablet) {
    font-size: 3.2em;
  }
}

.l-about__txt {
  font-size: 1.4rem;
  line-height: 1.8;

  @include g.mq(tablet) {
    font-size: 1.6rem;
  }
}



.l-about__btn {
  text-align: center;
  margin-top: 40px;
}