@use "../global" as g;

.l-footer {
  background-color: g.$color-text;
  padding-top: 50px;
  padding-bottom: 50px;

  @include g.mq(pc) {
    padding-top: 100px;
    padding-bottom: 100px;
  }
}

.l-footer__inner {
  width: 200px;
  margin: 0 auto;
}

.l-footer__logoImg {
  width: 87px;
  margin: 0 auto;

  @include g.mq(pc) {
    width: 180px;
  }
}