@use "global" as g;

// MV fade-in
@keyframes fadeInMv {
  100% {
    opacity: 1;
  }
}

// MV animation
.bgAnimate {
  opacity: 0;
  overflow: hidden;
  animation: 1s mvbgAnimate 0.5s forwards;
}
@keyframes mvbgAnimate {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.bgAnimateSecond {
  opacity: 0;
  animation: 1s mvbgAnimateSecond 1.8s forwards;
}
@keyframes mvbgAnimateSecond {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.LRanimate::before {
  content: "";
  position: absolute;
  top: 0px;
  width: 100%;
  height: 100%;
  background-color: g.$color-primary;
  animation: 2s mvLRanimate 0.5s forwards;
}
@keyframes mvLRanimate {
  0% {
    transform: scaleX(0);
    transform-origin: left;
  }
  50% {
    transform: scaleX(1);
    transform-origin: left;
  }
  50.001% {
    transform-origin: right;
  }
  100% {
    transform: scaleX(0);
    transform-origin: right;
  }
}

// About fade-up
.fadeUpEffect {
  opacity: 0;
}

.fadeUp {
  opacity: 0;
  animation-name: fadeUp;
  animation-duration: 0.8s;
  animation-fill-mode: forwards;
}
@keyframes fadeUp {
  from {
    opacity: 0;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}