@use "../../global" as g;

.c-accordion {
  margin-top: -20px;
}

.c-accordion__item {
  border-bottom: 1px solid #ccc;
}

.c-accordion__label {
  flex: 0 0 25px;
  font-size: 2.6rem;
  font-weight: bold;
  color: g.$color-primary;
}

.c-accordion__txt {
  font-size: 1.5rem;
  line-height: 1.7;
  margin-top: 3px;
}

.c-accordion__question{
  font-weight: normal;
  padding: 20px 30px 15px 0;
  position: relative;
}
.c-accordion__question::after {
  font-family: 'Material Icons Round';
  content: '\e5cf';
  font-size: 2.4rem;
  color: g.$color-primary;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY( -50%);
}
.c-accordion__question.active::after {
  content: '\e5ce';
}

.c-accordion__questionInner {
  display: flex;
  column-gap: 20px;
}






.c-accordion__answer {
  display: none;
  padding-bottom: 15px;
}
.c-accordion__answer.active {
  display: block;
}

.c-accordion__answerInner {
  display: flex;
  column-gap: 20px;
}