@use "../../global" as g;

.c-priceCard {
  width: 290px;
  color: g.$color-text;
  background-color: g.$color-white;
  text-align: center;
  padding: 35px 20px 5px;
  border-radius: 10px;
  position: relative;

  @include g.mq(pc) {
    width: 350px;
    padding: 35px 20px 10px;
  }
}

.c-priceCard__Ttl {
  font-size: 2.6rem;
  color: g.$color-primary;
}

.c-priceCard__Price {
  padding-top: 11px;
  padding-bottom: 28px;

  @include g.mq(pc) {
    padding-top: 6px;
    padding-bottom: 17px;
  }
}

.c-priceCard__pricePrice {
  font-size: 4.8rem;
}

.c-priceCard__priceNote {
  font-size: 1.6rem;

  @include g.mq(pc) {
    font-size: 1.8rem;
  }
}

.c-priceCard__item {
  padding: 27px 0;
  border-top: 1px solid #ccc;

  @include g.mq(pc) {
    padding: 18px 0;
  }
}

.c-priceCard__itemTtl {
  font-size: 1.8rem;
}

.c-priceCard__itemTxt {
  font-size: 1.6rem;
  margin-top: 4px;
}

.c-priceCard__label {
  width: 80%;
  font-size: 2.0rem;
  font-weight: bold;
  line-height: 1;
  color: g.$color-primary;
  background-color: g.$color-white;
  padding: 8px 0;
  border: 2px solid g.$color-primary;
  border-radius: 50px;
  position: absolute;
   top: -22px;
   left: 50%;
  transform: translateX(-50%);
}
