@use "../global" as g;

.l-section {
  padding-top: 40px;
}

.l-section--colorReverse {
  width: 100%;
  color: g.$color-white;
  background-color: g.$color-primary;
}

.l-section__ttl {
  font-size: 4rem;

  @include g.mq(pc) {
    font-size: 6.4rem;
  }
}
.l-section__ttl--jp {
  display: block;
  font-size: 1.4rem;
  color: g.$color-primary;

  @include g.mq(pc) {
    font-size: 2.0rem;
  }
}
.l-section--colorReverse .l-section__ttl--jp {
  color: g.$color-white;
}