@use "../../global" as g;

.c-btn {
  display: inline-block;
  width: 100%;
  font-size: 1.3rem;
  font-weight: bold;
  color: #fff;
  background-color: g.$color-primary;
  line-height: 1;
  text-align: center;
  padding: 20px 10px;
  border-radius: 50px;

  @include g.mq(pc) {
    font-size: 2rem;
  }
}
.c-btn--header {
  font-size: 12px;
  padding: 12px 15px;
}
.c-btn--mv {
  max-width: 220px;
  padding: 16px 10px;

  @include g.mq(pc) {
    max-width: 370px;
  }
}

.c-btn--about{
  max-width: 300px;

  @include g.mq(tablet) {
    display: none;
  }
}

.c-btn--cta {
  max-width: 300px;
  font-weight: normal;
  color: g.$color-primary;
  background-color: #fff;

  @include g.mq(tablet) {
    max-width: 360px;
  }
}
