@use "../../global" as g;

.c-pageTop {
  display: none;
  width: 40px;
  height: 40px;
  text-align: center;
  background-color: g.$color-white;
  border-radius: 50px;
  box-shadow: 0 0 10px rgb(0, 0, 0, 20%);
  cursor: pointer;
  position: fixed;
    right: 30px;
    bottom: 20px;
  z-index: 100;

  @include g.mq(pc) {
    width: 80px;
    height: 80px;
  }
}

.c-pageTop__icon {
  font-size: 3.0rem;
  color: g.$color-primary;
  padding: 5px;
  
  @include g.mq(pc) {
    font-size: 6.0rem;
    padding: 10px;
  }
}